import React from 'react';
import paint from '../resources/paint.jpg'
import guitar from '../resources/guitar.jpg'
import code from '../resources/code.jpg'

const Showcase = () => {

    return (
        <>
            <div className="maincontainer">
                <h2>Who am I?</h2>
                <div className="col-sm-4">
                    <img src={code} alt="code" width="100%"></img>
                    <p>I'm a programmer. I spend most of my time learning new technologies and programming languages. </p>
                </div>
                <div className="col-sm-4">
                    <img src={paint} alt="paint" width="100%"></img>
                    <p>I'm an artist. I love visiting art museums and painting. </p>
                </div>

                <div className="col-sm-4">
                    <img src={guitar} alt="guitar" width="100%"></img>
                    <p>I'm a musician. I use the guitar to express the melody in my mind.</p>
                </div>
            </div>
        </>
    )

}


export default Showcase;

